.page {
  box-sizing: border-box;
  letter-spacing: 0;
  color: #060b35;
  font-family: Roboto, sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-wrap: wrap;
  justify-content: center;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__product-image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  margin-left: 0;
}

.card__product-image > img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.card__product-name {
  letter-spacing: 0;
  width: 100%;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  letter-spacing: 0;
  color: #616070;
  width: 100%;
  height: 14px;
  margin-bottom: 17px;
  font-size: 10px;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 16px;
  margin-bottom: 23px;
  display: flex;
}

.card__num-of-reviews {
  letter-spacing: 0;
  text-align: right;
  width: 53px;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  width: 100%;
  height: 18px;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  letter-spacing: 0;
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  letter-spacing: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  text-transform: uppercase;
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.stars {
  flex-direction: row;
  max-width: 96px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

